  box-sizing: inherit;
}

nav,
footer,
header,
aside {
  display: block;
}

input::-ms-clear {
  display: none;
}

a {
  color: var(--color-dove-gray);
  transition: color 0.2s ease 0s;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active,
a:focus {
  outline: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: top;
}

button:active,
button:focus {
  outline: none;
}

.fa {
  color: var(--color-white);
  transition: color 0.2s ease 0s;
}

html {
  box-sizing: border-box;
  font-size: 14px;
}

body {
  font-family: 'OpenSans', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: var(--color-gray);
  background-color: var(--color-white);
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100vh;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

._container {
  max-width: 1210px;
  padding: 0 20px;
  margin: 0 auto;
}

._btn-reset {
  cursor: pointer;
  background: transparent;
  border: none;
  transition: background 0.2s ease 0s;
}

._mid-font {
  font-family: 'NotoSans', sans-serif;
  font-size: 1.28rem;
  font-weight: 700;
  color: var(--color-dove-gray);
  text-transform: uppercase;
  letter-spacing: 0.1px;
}

._white-font {
  position: relative;
  font-size: 1.71rem;
  font-weight: 700;
  line-height: 2.57rem;
  color: var(--color-white);
  text-shadow: 1px 1px 1px #909090;
  text-transform: uppercase;
  letter-spacing: 0.1px;
}

._blue-title {
  margin-left: 15px;
  font-family: 'NotoSans', sans-serif;
  font-size: 1.71rem;
  font-weight: 700;
  line-height: 4.28rem;
  color: var(--color-dodger-blue);
  text-transform: uppercase;
}

._blue-title::before {
  position: absolute;
  top: 0;
  left: -28px;
  content: url("../img/logo-small.png");
}

._line {
  position: relative;
  display: inline-block;
}

._line::after {
  position: absolute;
  right: -10px;
  width: 50px;
  height: 3px;
  content: '';
  box-shadow: 1px 1px 2px #bebebe;
  transition: background-color 0.3s ease 0s;
}

._line-wh::after {
  top: 28px;
  background-color: var(--color-white);
}

._line-bl-1::after {
  top: 38px;
  background-color: var(--color-dodger-blue);
}

._line-gray::after {
  top: 49px;
  background-color: var(--color-silver-chalice);
}

._line-bl-2::after {
  top: 28px;
  background-color: var(--color-dodger-blue);
  box-shadow: 1px 1px 2px #282828;
}

._cntr-title {
  text-align: center;
}

.fa-angle-left {
  padding-right: 3px;
}

.fa-angle-right {
  padding-left: 3px;
}

@keyframes fade-in {
  from {
    opacity: 0.1;
  }

  to {
    opacity: 1;
  }
}

._flex-wrap {
  display: flex;
  flex-wrap: wrap;
}

._active-menu {
  color: var(--color-dodger-blue);
}

._disable-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.noscript-info {
  position: absolute;
  top: 63px;
  z-index: 100;
  width: 100%;
  padding: 25px;
  font-size: 2rem;
  line-height: 2.57rem;
  color: var(--color-black);
  text-align: center;
  background-color: #f5a416;
}

.noscript-info a {
  color: #06c;
  text-decoration: underline;
}

.header {
  position: fixed;
  right: 0;
  left: 0;
  z-index: 50;
  background-color: var(--color-alabaster);
  box-shadow: 0 1px 4px rgba(50, 50, 50, 0.3);
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  display: inline-block;
  flex-shrink: 0;
  width: 194px;
  height: 63px;
  padding-top: 11px;
}

.menu-header__burger {
  display: none;
}

.menu-header__burger._active span {
  transform: scale(0);
}

.menu-header__burger._active span:first-child {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.menu-header__burger._active span:last-child {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}

.nav {
  padding: 21px 0;
}

.nav__list {
  display: flex;
  gap: 36px;
  transition: gap 0.3s ease 0s;
}

.nav__link {
  position: relative;
  text-transform: uppercase;
}

.nav__link:hover::before {
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 3px;
  content: '';
  background-color: var(--color-dodger-blue);
}

.nav._active .nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav._active .nav__link {
  color: var(--color-light-gray);
  text-shadow: 2px 2px 2px black;
}

.intro {
  position: relative;
  height: calc(100vh - 63px);
  margin-top: 63px;
  background: url("../img/intro.jpg") center no-repeat;
  background-color: var(--color-gray);
  background-size: cover;
  transition: height 0.3s ease 0s;
}

.intro__container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  padding: 0 9%;
}

.intro__inner {
  z-index: 2;
}

.intro__title {
  margin-bottom: 12px;
  font-size: 4.5rem;
  font-weight: 800;
  line-height: 1.3;
  color: var(--color-white);
  text-align: right;
  text-shadow: 1px 1px 1px #323232;
  transition: font-size 0.2s ease 0s;
}

.intro__title-mercury {
  color: var(--color-mercury);
}

.intro__title-alabaster {
  color: var(--color-alabaster);
}

.intro__title-blue {
  color: var(--color-dodger-blue);
}

.intro__button-inner {
  text-align: right;
}

.intro button {
  font-family: 'OpenSans', sans-serif;
  text-transform: uppercase;
}

.intro__button-left {
  padding: 0 35px;
  margin-right: 17px;
  line-height: 4.28rem;
  background-color: var(--color-alabaster);
}

.intro__button-right {
  padding: 0 35px;
  line-height: 4.28rem;
  color: var(--color-white);
  background-color: var(--color-tundora);
}

.about__container {
  padding: 118px 20px 75px;
}

.about__list {
  gap: 30px;
  justify-content: center;
}

.item-about {
  position: relative;
  width: 370px;
  padding: 50px 32px 22px;
  cursor: default;
  background-color: var(--color-wild-sand);
}

.item-about__icon-inner {
  position: absolute;
  top: -35px;
  left: 30px;
  width: 70px;
  height: 70px;
  background-color: var(--color-white);
  border-radius: 100%;
}

.item-about__icon {
  width: 60px;
  height: 60px;
  margin: 0 auto;
  margin-top: 5px;
  font-size: 30px;
  line-height: 60px;
  text-align: center;
  background-color: var(--color-tundora);
  border-radius: 100%;
  transition: background-color 0.2s ease 0s;
}

.item-about__title {
  display: inline-block;
  padding-bottom: 22px;
}

.choose {
  display: flex;
}

.choose__img {
  flex: 1 1 auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.choose__left {
  background-image: url("../img/choose-left-bg.jpg");
}

.choose__right {
  max-width: 26%;
  background-image: url("../img/choose-right-bg.jpg");
}

.choose-center {
  max-width: 670px;
  padding: 52px 40px 60px;
  background-color: var(--color-wild-sand);
}

.choose-center__title {
  position: relative;
  margin-bottom: 44px;
}

.choose-center__text {
  margin-bottom: 40px;
}

.choose-center__list {
  gap: 15px;
  justify-content: space-between;
}

.choose-item {
  position: relative;
  width: 273px;
  padding: 0 20px 15px 70px;
  margin: 0 auto;
  cursor: default;
}

.choose-item__icon-inner {
  position: absolute;
  top: 0;
  left: 0;
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  font-size: 23px;
  line-height: 50px;
  text-align: center;
  background-color: var(--color-white);
}

.choose-item__icon-inner .fa {
  color: var(--color-tundora);
}

.choose-item__title {
  margin-bottom: 8px;
  font-family: 'NotoSans', sans-serif;
  font-weight: 700;
}

.what__container {
  padding: 63px 20px 88px;
}

.what__title-inner {
  text-align: center;
}

.what__pagination {
  display: none;
}

.item-what {
  position: relative;
  width: 274px;
}

.item-what__title {
  display: inline-block;
  margin: 13px 0 15px 6px;
  line-height: 24px;
}

.item-what__icon-inner {
  display: inline-block;
  width: 43px;
  height: 43px;
  font-size: 18px;
  line-height: 43px;
  text-align: center;
  background-color: var(--color-tundora);
  border-radius: 100%;
  transition: background-color 0.3s ease 0s;
}

.work {
  background-image: url("../img/work-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.work__container {
  padding: 84px 20px;
}

.work__center {
  max-width: 870px;
  padding: 35px 40px 44px;
  background-color: rgba(255, 255, 255, 0.9);
}

.work__title {
  margin-bottom: 20px;
  font-family: 'NotoSans', sans-serif;
  font-size: 1.71rem;
  font-weight: 700;
  line-height: 2.57rem;
  color: var(--color-dove-gray);
  text-transform: uppercase;
}

.work__list {
  gap: 30px;
  justify-content: center;
  margin-top: 28px;
}

.work-item {
  display: flex;
  gap: 12px;
  width: 243px;
}

.work-item__icon {
  flex-shrink: 0;
  width: 35px;
  height: 35px;
  font-size: 21px;
  line-height: 35px;
  text-align: center;
  background-color: #fff;
  border-radius: 100%;
  box-shadow: 1px 1px 2px #bebebe;
}

.work-item__icon .fa {
  color: var(--color-tundora);
}

.work-item__title {
  display: inline-block;
  margin-bottom: 5px;
}

.team__container {
  padding: 63px 20px 84px;
}

.team__articles {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 108px;
}

.team-article {
  position: relative;
  max-width: 570px;
  min-height: 211px;
  margin-bottom: 12px;
  background-color: var(--color-wild-sand);
}

.team-article:last-child {
  padding-bottom: 40px;
}

.team-article__left {
  position: absolute;
  top: -20px;
  left: 30px;
  z-index: 10;
  width: 155px;
}

.team-article__img {
  box-sizing: content-box;
  width: 135px;
  height: 160px;
  margin-bottom: 5px;
  color: var(--color-white);
  background-color: var(--color-gray);
  border: 10px solid var(--color-mercury);
}

.team-article__sicial-inner {
  display: flex;
  gap: 5px;
  justify-content: center;
}

.team-article__sicial-icon {
  flex-shrink: 0;
  width: 35px;
  font-size: 18px;
  line-height: 35px;
  text-align: center;
  background-color: var(--color-white);
  border-radius: 100%;
  box-shadow: 1px 1px 2px #bebebe;
}

.team-article__sicial-icon .fa {
  color: var(--color-tundora);
}

.team-article__right {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 19px 25px 19px 0;
  margin-left: 185px;
}

.team-article__title-inner {
  position: relative;
  z-index: 6;
  max-width: 100%;
  height: 50px;
  padding: 0 50px 0 17px;
  color: var(--color-dove-gray);
  text-transform: uppercase;
  background-color: #fff;
  box-shadow: 1px 1px 0 #c3c3c3;
  transition: color 0.2s ease 0s;
}

.team-article__title-inner::after {
  position: absolute;
  top: 0;
  right: -7px;
  z-index: 5;
  width: 18px;
  height: 50px;
  content: '';
  background-color: #fff;
  box-shadow: 2px 0 0 #c3c3c3;
  transform: skew(-17deg);
}

.team-article__title {
  padding: 10px 0 19px;
  overflow: hidden;
  font-size: 1rem;
  font-weight: 700;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.team-article__status-inner {
  position: relative;
  z-index: 7;
  display: inline-block;
  max-width: 330px;
  height: 35px;
  padding: 0 27px 0 17px;
  margin: -10px 20px 0 0;
  text-transform: uppercase;
  background-color: var(--color-mercury);
  box-shadow: 1px 1px 0 #c3c3c3;
}

.team-article__status-inner::after {
  position: absolute;
  top: 0;
  right: -6px;
  width: 14px;
  height: 34.9px;
  content: '';
  background-color: var(--color-mercury);
  box-shadow: 2px 0 0 #c3c3c3;
  transform: skew(-22deg);
}

.team-article__status {
  padding: 7px 0;
  overflow: hidden;
  font-size: 0.86rem;
  font-weight: 600;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.team-article__text {
  padding: 16px 0 0 17px;
}

.quality {
  background-image: url("../img/quality-bg.jpg");
  background-position: center;
  background-size: cover;
}

.quality__container {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  padding: 54px 20px 74px;
}

.quality__left {
  flex: 0 1 auto;
  max-width: 100%;
  margin-top: 20px;
}

.quality__right {
  flex: 1 1 auto;
  max-width: 450px;
  padding: 0 20px 22px;
}

.quality__empty {
  flex: 1 1 auto;
}

.quality-right__title {
  margin-bottom: 27px;
}

.quality-right__text {
  margin-bottom: 22px;
}

.quality-item {
  display: flex;
}

.quality-item:not(:last-child) {
  margin-bottom: 20px;
}

.quality-item__icon {
  flex-shrink: 0;
  width: 35px;
  height: 35px;
  font-size: 14px;
  line-height: 35px;
  text-align: center;
  background-color: #fff;
  border-radius: 100%;
  box-shadow: 1px 1px 2px #bebebe;
}

.quality-item__icon .fa {
  color: var(--color-tundora);
  transform: rotate(180deg);
}

.quality-item__text {
  margin-left: 19px;
}

.portfolio__container {
  max-width: 1214px;
  padding: 63px 20px 80px;
}

.portfolio__title {
  margin-bottom: 44px;
}

.portfolio__filter-list {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.portfolio__filter-button {
  padding: 0 20px;
  font-size: 12px;
  line-height: 30px;
  color: inherit;
  text-transform: uppercase;
  cursor: pointer;
  background-color: var(--color-wild-sand);
}

.portfolio__swiper-wrapper {
  margin-top: 56px;
}

.portfolio__item {
  position: relative;
  width: 292px;
  height: 210px;
  overflow: hidden;
  background-color: rgba(58, 163, 251, 0.7);
}

.portfolio__item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.portfolio__hide-work {
  display: none;
}

.portfolio__fade-in {
  animation-name: fade-in;
  animation-duration: 0.7s;
}

.portfolio__active-btn {
  color: var(--color-tundora);
  background-color: var(--color-alto);
}

.portfolio__pagination {
  display: none;
}

.hover-item {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  background-color: rgba(58, 163, 251, 0.8);
  opacity: 0;
  transition: opacity 0.3s ease 0s;
}

.hover-item .fa {
  color: var(--color-dove-gray);
  transition: color 0.2s ease 0s;
}

.hover-item a {
  display: block;
  width: 40px;
  height: 40px;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  background-color: #fff;
  border-radius: 100%;
  transition: background-color 0.3s ease 0s;
  transition: transform 0.3s ease 0s;
  transform: scale(2);
}

.hover-item__descr {
  display: none;
}

.best {
  background-image: url("../img/best-bg.jpg");
  background-position: center;
  background-size: cover;
}

.best__container {
  display: flex;
}

.best__left {
  flex: 1 1 auto;
}

.best__right {
  flex: 1 1 auto;
  max-width: 960px;
}

.best-right {
