  text-transform: uppercase;
}

.item-right__text {
  margin-bottom: 11px;
}

.blog__container {
  padding: 63px 20px 40px;
}

.blog__control {
  display: flex;
  justify-content: space-between;
  height: 35px;
  margin-top: 40px;
}

.blog__arrows {
  position: relative;
}

.blog__button {
  width: 35px;
  height: 35px;
  padding-bottom: 4px;
  font-size: 24px;
  background-color: var(--color-tundora);
  border-radius: 100%;
}

.blog__link {
  display: block;
  height: 35px;
  padding: 0 30px;
  font-size: 0.86rem;
  font-weight: 700;
