.testi {
  background: linear-gradient(0deg, #f4f4f4 0, white 100%);
}

.testi__container {
  display: flex;
  gap: 7px;
  justify-content: space-between;
  padding-top: 81px;
}

.testi__slider {
  position: relative;
  max-width: 733px;
  overflow: hidden;
}

.testi__right {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.testi__arrows {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 15;
}

.testi-slide {
  display: flex;
  justify-content: space-between;
}

.testi-slide__image {
  position: relative;
  flex-shrink: 0;
  min-width: 236px;
  max-width: 291px;
  max-height: 423px;
}

.slide-left__speech {
  position: relative;
  max-width: 428px;
  max-height: 276px;
  padding: 45px 30px 30px;
  margin-top: 47px;
  background-color: #fff;
}

.slide-left__speech blockquote {
  max-height: 170px;
  overflow: hidden;
}

.slide-left__speech-quote {
  position: absolute;
  top: -28px;
  left: 30px;
  width: 67px;
  height: 57px;
  font-size: 5.15rem;
}

.slide-left__speech-quote .fa {
  color: var(--color-dodger-blue);
}

.left-info {
  padding: 16px 0;
  margin-right: 26px;
  color: var(--color-dove-gray);
  text-align: right;
}

.left-info__author {
  font-weight: 700;
  text-transform: uppercase;
